div {
  box-sizing: border-box;
}

.container {
  display: flex;
}

.leftContent {
  flex: 1;
  height: 100vh;
  overflow-y: auto;
  padding: 0 5px 15px 5px;
}

.itemContainer {
  padding-top: 10px;
}

.rightNav {
  width: 100px;
  height: 100vh;
  overflow-y: auto;
  padding: 15px 5px;
}

.rightNav > div {
  width: 100%;
}

.wrapAnchor {
  margin-top: 10px;
  height: calc(100vh - 90px);
  overflow-y: auto;
}

.tagStyle {
  font-size: 14px;
  cursor: pointer;
  display: inline-block;
  width: 105px;
  line-height: 25px;
  margin-right: 8px;
  margin-bottom: 6px;
  border: 1px solid #d9d9d9;
  padding: 0 7px;
  border-radius: 3px;
}

.paddingRight20 {
  margin-top: 12px;
  margin-left: 20px;
  color: #e2682b;
  font-size: 16px;
}

.paddingRight40 {
  margin-left: 40px;
  font-size: 14px;
  padding: 8px 0;
}

.paddingRight60 {
  margin-left: 60px;
}

.pointer {
  cursor: pointer;
}
