.container {
  position: fixed;
  right: 0;
  top: 0;
  width: 40%;
  height: 5%;
  display: flex;
  align-items: baseline;
  padding: 0 6px;
  box-sizing: border-box;
}

.website {
  font-size: 18px;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.8);
}

.intro {
  flex: 1;
  padding: 0 15px 0 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.8);
  cursor: pointer;
}

.modalContent {
  height: 80vh;
}
